body {
  background: var(--color-background-light);
  background: #0e1129;
  height: 100vh;
  width: 100%;
  overflow-x: hidden;
  position: relative;
}

html {
  font-size: 100%;
}

.container {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-left: 20px;
  padding: 0.5rem;
  overflow-x: hidden;
}

.container::after {
  content: '';
  position: absolute;
  left: 0;
  top: 10%;
  width: 2px;
  height: 550px;
  background: -webkit-gradient(linear, left top, left bottom, from(#db6f0b), to(#4000ff));
  background: linear-gradient(#db6f0b, #4000ff);
}

.title {
  font-size: 4.5rem;
  color: white;
  font-weight: bold;
  text-align: center;
}

.left-col {
  margin-top: 60px;
  width: 600px;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 1;
}

.left-col form {
  margin-top: 30px;
  position: relative;
}

.form {
  position: relative;
  width: 20rem;
  height: 3rem;
  margin-bottom: 20px;
}

.form__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #ccc;
  border-radius: 0.5rem;
  font-family: inherit;
  font-size: inherit;
  color: white;
  outline: none;
  padding: 1.25rem;
  background: none;
}

.form__input:hover {
  border-color: darkcyan;
}

.form__input:focus {
  border-color: #4000ff;
}

.form__label {
  position: absolute;
  left: 1rem;
  top: 0.8rem;
  padding: 0 0.5rem;
  color: white;
  cursor: text;
  background: #0e1129;
  -webkit-transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
  -webkit-transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
  transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
}

.form__input:focus ~ .form__label,
.form__input:not(:placeholder-shown).form__input:not(:focus) ~ .form__label {
  top: -0.5rem;
  font-size: 0.8rem;
  left: 0.8rem;
}

.btn {
  padding: 0.5rem 1.9rem;
  border: 0px;
  font-size: 1.2rem;
  border-radius: 2px;
  color: white;
  background: transparent;
  border: 1px solid white;
  cursor: pointer;
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
  border-radius: 0.2rem;
}

.login-btn,
.register-btn {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

button:hover {
  background: #4000ff;
  border: 1px solid #4000ff;
}

.message {
  color: white;
  margin-top: 60px;
  margin-bottom: 20px;
}

.reg-label {
  margin-top: 15px;
  color: white;
}

.spline {
  position: absolute;
  top: -2%;
  right: -5%;
}

@media screen and (max-width: 940px) {
  html {
    font-size: 90%;
  }
  .container {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
}

@media screen and (max-width: 540px) {
  html {
    font-size: 70%;
  }
  .left-col {
    margin-top: 10px;
  }
}

@media screen and (max-width: 370px) {
  .btn {
    font-size: 0.9rem;
  }
}
