.post-main {
  width: 85%;
  margin-left: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
  min-height: 100vh;
  background: var(--color-background-light);
}

.post-container {
  width: 80%;
  max-width: 850px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.post-title {
  color: var(--color-title-light);
  font-weight: normal;
  font-size: 2.4rem;
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-bottom: 1.2rem;
}

.post-form {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 30px;
}

.post-form .left-col-post {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  gap: 10px;
}

.post-form .right-col-post {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 10px;
}

.post-form .input-group {
  color: var(--color-text-light);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 5px;
  position: relative;
  font-size: 1.3rem;
}

.post-form .input-group input {
  height: 10px;
  width: 100%;
  border: 2px solid var(--color-text-light);
  font-family: inherit;
  font-size: inherit;
  border-radius: 0.5rem;
  padding: 1.25rem;
  background: none;
  color: var(--color-text-light);
}

.post-form .input-group textarea {
  resize: none;
  border: 2px solid var(--color-text-light);
  font-family: inherit;
  font-size: inherit;
  border-radius: 0.5rem;
  padding: 0.8rem;
  background: none;
  color: var(--color-text-light);
  height: 220px;
}

.post-form .input-group label {
  -webkit-box-ordinal-group: 0;
      -ms-flex-order: -1;
          order: -1;
  font-size: 1.4rem;
}

.post-form .image-container {
  border-radius: 0.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 220px;
  width: 100%;
  border: 2px solid var(--color-text-light);
  position: relative;
  border-style: dotted;
}

.post-form .image-container img {
  padding: 1rem;
  border-radius: 0.5rem;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  position: absolute;
}

.post-form .image-container span {
  text-decoration: underline;
  cursor: pointer;
}

.post-form .image-container p {
  text-align: center;
  color: var(--color-text-light);
}

.post-form .image-container .img-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 15px;
}

.post-form .image-container .img-info p:last-child {
  color: var(--color-text-light);
  font-size: 0.9rem;
}

.post-form .image-container .img-info .icon {
  font-size: 2.5rem;
}

.post-form .buttons {
  gap: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.danger {
  background: #fc5f5f;
  border: 1px solid #fc5f5f;
}

.primary {
  background: #4d58ec;
  border: 1px solid #4d58ec;
}

.upload {
  font-size: 5rem;
  color: var(--color-title-light);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
}

#dark .upload {
  color: white;
}

.photo {
  position: relative;
}

.progress-bar {
  position: absolute;
  top: 73%;
  left: 38%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 3;
}

.progress-bar .progress-bar-text {
  text-decoration: none;
  display: none;
}

@media screen and (max-width: 720px) {
  .post-main {
    width: 100%;
  }
  .post-container {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .post-form {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}
